* {
	border: 0px;
	padding: 0px;
	margin: 0px;
	box-sizing: border-box;
	font-family: Arial, Helvetica, sans-serif;
}
.app-noresult {
	text-align: center;
	margin-top: 3rem;
	border-radius: 0.4rem;
	background-color: beige;
	width: 500px;
}
.app-container {
	/* text-align: center; */
	overflow-x: auto;
	height: 100vh;
	width: 100vw;
}

.app-navbar {
	/* flex-props */
	display: flex;
	/* justify-content: space-around; */
	align-items: center;
	/* generic props */
	background-color: #4758b6;
	height: 5rem;
}

li {
	list-style-type: none;
	padding: 0px;
	margin: 0px;
	border: 0px;
	line-height: 1.5;
	/* display: inline-flex; */
}

.app-logo {
	/* flex-props */
	/* order: 0; */
}
.app-name {
	/* flex-props */
	/* order: 1; */
	margin-left: 1rem;
	color: white;
}

.app-searchbar {
	/* flex-props */
	/* order: 2;
	flex-grow: 1; */
	/* generic-props */
	margin-left: 4rem;
	width: 60rem;
	height: 3rem;
	outline: none;
	padding: 0 10px;
	border-radius: 0.5rem;
	position: sticky;
}

.app-result-container {
	display: flex;
	justify-content: center;
	align-items: center;
	flex-wrap: wrap;
}

.country-card {
	height: 153px;
	width: 200px;
	margin: 0.5rem;
	border-radius: 0.5rem;
	padding: 10px;
	/* border:10px #f1f2f8 solid ; */
	background-color: #3a22ff17;

	overflow: hidden;
}
.country-flag {
	margin-left: 20%;
	margin-top: 3%;
}

.country-name {
	margin-top: 15px;
	font-weight: bolder;
	text-align: center;
	font-size: smaller;
	/* margin-left: 2.1rem; */
	line-height: 1;
}

.country-modal-popup {
	background-color: rgb(246 249 255 / 82%);
	width: 70rem;
	top: 6rem;
	left: 13rem;
	height: 38rem;
	position: absolute;
	border-radius: 0.5rem;
	z-index: 10;
	padding: 3rem;
}

.btn-close {
	float: right;
	margin: 1rem;
	font-size: larger;
}

.country-modal-flag {
	width: 24rem;
	height: 14rem;
}

.country-modal-name {
	font-family: sans-serif;
	font-size: xxx-large;
}
.country-modal-summary {
    margin-top: 2rem;
}

.country-modal-label {
    font-weight: bolder;
    font-size: x-large;
	font-variant: all-small-caps;
}

.country-modal-label-value {
    font-size: large;
    font-variant: all-small-caps;
}

.country-modal-desc {
    width: 30rem;
    position: absolute;
    left: 31rem;
    top: 9rem;
}